.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


#menuheader, .menu-option {
  color: green;
  font-family: times;
  font-size: 50px;
  margin-left: 100px;
  text-align: right;
  margin-right: 15px;
  background-color: yellow;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.post-container {
    overflow: auto
}
.post-thumb {
    float: left
}
.post-pinky {
  float: right;
}
.post-thumb img {
    display: block
}
.post-content {
    margin-left: 210px
}
.blue-bg{
  background-color: lightblue;
}

.green-bg{
  background-color: lightgreen;
}
